<template>
  <div>
    <!-- -->
    <el-card style="background: none;background-color: transparent">
      <div style="margin-bottom: 10px;">
        <el-input style="width: 200px;margin-left: 130px;" placeholder="" v-model="name"></el-input>
        <el-select v-model="type" placeholder="" style="margin-left: 5px;">
          <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
        <el-select v-model="generation" placeholder="" style="margin-left: 5px;">
          <el-option v-for="item in optionsGeneration" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
        <el-button type="primary" @click="load" style="margin-left: 5px;"></el-button>
        <el-button type="info " @click="reset"></el-button>
      </div>
    </el-card>
    <el-card style="margin: 10px 0 0 90px;width: 90%;">
      <el-table :data="filteredData" border style="width: 100%">
        <el-table-column prop="id_str" label="" width="180" align="center">
        </el-table-column>
        <el-table-column label="" width="180">
          <template v-slot:default="scope">
            <el-image style="width: 100px; height: 100px" :src="getImage(scope.row.id)"  @click="details(scope.row)" fit="fit">
            </el-image>
          </template>
        </el-table-column>
        <el-table-column style="font-size: 10px;" label="" width="180" align="center">
          <template v-slot:default="scope">
            <div>
              <a href="#" style="font-size: 20px;text-decoration: none;color:black" @click="details(scope.row)">
                {{ scope.row.name }}</a>
            </div>
          </template>
        </el-table-column>
        <el-table-column label="" width="180" align="center">
          <template v-slot:default="scope">
            <div style="display: flex; align-items: center; justify-content: space-evenly;">
              <div v-if="colorMap[scope.row.type1]" class="attribute-box"
                :style="{ backgroundColor: colorMap[scope.row.type1] }">
                <a href="#" style="text-decoration: none;color: #fff;" @click="loadType(scope.row.type1)">{{
                  scope.row.type1 }}</a>
              </div>
              <div v-if="scope.row.type2 && colorMap[scope.row.type2]" class="attribute-box"
                :style="{ backgroundColor: colorMap[scope.row.type2] }">
                <a href="#" style="text-decoration: none;color: #fff" @click="loadType(scope.row.type2)">{{
                  scope.row.type2 }}</a>
              </div>
            </div>
          </template>
        </el-table-column>
        <el-table-column label="" width="180" align="center">
          <template v-slot:default="scope">
            <div style="display: flex; align-items: center; justify-content: space-evenly;">
              <div class="attribute-ability">
                {{ scope.row.ability1 }}
              </div>
            </div>
          </template>
        </el-table-column>
        <el-table-column label="" width="180" align="center">
          <template v-slot:default="scope">
            <div style="display: flex; align-items: center; justify-content: space-evenly;">
              <div v-if="scope.row.type2" class="attribute-ability">
                {{ scope.row.ability2 }}
              </div>
              <div v-else class="attribute-ability">
                
              </div>
            </div>
          </template>
        </el-table-column>
        <el-table-column label="" width="180" align="center">
          <template v-slot:default="scope">
