              (item.type1.toLowerCase().includes(this.type.toLowerCase())||
              item.type2.toLowerCase().includes(this.type.toLowerCase())) &&
              item.generation.toLowerCase().includes(this.generation.toLowerCase())
      })
      .sort((a,b) => a.id_str - b.id_str)
    },
    reset() {
      this.name = ''
      this.type = ''
      this.generation = '1'
